import React from 'react'
import Layout from '../components/Layout'
import { graphql, useStaticQuery } from 'gatsby'
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import {Link} from 'gatsby';
import { BgImage } from "gbimage-bridge"

export default function ActiviteitenUk() {
  const { placeholderImage,  placeholderKlankschaal, placeholderAmarill1, placeholderAmarill2, placeholderEzinge, placeholderTempelCasa,
    placeholderCasaHuis, placeholderOmgevingCasa } = useStaticQuery(
        graphql`
          query {
            placeholderImage: file(relativePath: {eq: "Activiteiten Activities.JPG"}) {
              childImageSharp {
                gatsbyImageData(
                  width: 1600
                  quality: 80
                  webpOptions: {quality: 90}
                  formats: [AUTO, WEBP, AVIF]
                )
              }
            }
            placeholderKlankschaal: file(relativePath: {eq: "Klank.JPG"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderAmarill1: file(relativePath: {eq: "Amaryllis1.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderAmarill2: file(relativePath: {eq: "Amarillys2.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderEzinge: file(relativePath: {eq: "Kerkje Ezinge.jpg"}) {
              childImageSharp {
                  gatsbyImageData(
                    width: 800
                    quality: 80
                    webpOptions: {quality: 90}
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
          }
          placeholderTempelCasa: file(relativePath: {eq: "Tempel Casa de Nepal.jpg"}) {
              childImageSharp {
                  gatsbyImageData(
                    width: 800
                    quality: 80
                    webpOptions: {quality: 90}
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
          }
          placeholderCasaHuis: file(relativePath: {eq: "Casa de Nepal huis.jpg"}) {
              childImageSharp {
                  gatsbyImageData(
                    width: 800
                    quality: 80
                    webpOptions: {quality: 90}
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
          }
          placeholderOmgevingCasa: file(relativePath: {eq: "Omgeving Casa de Nepal met Anu.jpg"}) {
              childImageSharp {
                  gatsbyImageData(
                    width: 800
                    quality: 80
                    webpOptions: {quality: 90}
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
          }
          }
          
        `
      )
      const pluginImage = getImage(placeholderImage);
      const klankImage = getImage(placeholderKlankschaal);
      const AmarillImage1 = getImage(placeholderAmarill1);
      const AmarillImage2 = getImage(placeholderAmarill2);
      const Ezinge = getImage(placeholderEzinge);
      const Tempel = getImage(placeholderTempelCasa);
      const CasaHuis = getImage(placeholderCasaHuis);
      const OmgevingCasa = getImage(placeholderOmgevingCasa);

    return (
        <Layout>
        <BgImage image={pluginImage} className="bg"
      Tag="section">
          <div className="actHeading">
          <h1 className="actHeading">ACTIVITIES</h1>
          </div>
          </BgImage>
          <div className="genText">
          <p>
          It is important that whatever we do flows naturally. It should never 
          be a battle. Pure love directs the flow of our lives and work, which 
          we share through Aikyam in the Netherlands and beyond. 
              </p>
              <h3>BIWEEKLY SOUND MEDITATION</h3>
              <p>
              Anu and Martien play the singing bowls every other week at course centre De Poort in Groningen.  <br/>
            For more information and to sign up: <a href="https://www.cursuscentrumdepoort.nl/klankmeditatie.html">website course centre De Poort</a>
              </p>
              <div className="centerImage">
              <GatsbyImage image={klankImage} alt={"De klankschaal"}/>
              </div>
              <h3>SING-IN EZINGE</h3>
              <p>
              relaxation, silence, connection and joy 
              </p>
              <p>
              Mantra singing<br/>
              Friday 12 November<br/>
              Friday 10 December<br/>
              Dates for 2022 will follow
              </p>
              <p>
              Singing mantras together gives energy and joy. Your own sound merges with all the other sounds into something beautiful. The acoustics of the small medieval church brings it all together. 
              Everyone can join. We sing mantras from different countries and eras. 
              </p>
              <p>
              Address: Torenstraat 46, on top of the mound in Ezinge<br/>
              From 7:15 - 8:15pm (open at 7pm)
              </p>
              <p>
              We finish with a cup of tea. <br/>
              Contribution: €3<br/>
              If you wish to join once or more please contact <a href="mailto:martien@aikyam.nl">martien@aikyam.nl</a> to sign up
              </p>
              <div className="centerImage">
              <GatsbyImage image={Ezinge} alt={"Church Ezinge"}/>
              </div>

              <h3>RETREAT HOME IN CENTRAL SPAIN</h3>
              <p>
              Our Spanish sister organisation We Are One is renting a natural stone and clay house at the edge of a tiny village situated in a natural reserve north of Madrid: La Casa de Nepal.
              </p>
              <p>
              Together with We Are One we give short and longer retreats where you experience how to live in a simple and harmonious way, close to nature. <br/>
              A regular day at Casa de Nepal:<br/>
              From 6:20am onwards silent meditation until about 7:30am, followed by mantra singing<br/>
              Short break<br/>
              Yoga from 9-10:30am<br/>
              Fresh fruit<br/>
              Cooked meal at 11am<br/>
              Free time to go for a walk in the beautiful mountains or to draw mandalas at the veranda, or...<br/>
              Dinner at 7pm<br/>
              From 8pm onwards silent meditation for about 45 minutes followed by mantra singing for at least half an hour<br/>
              Silence, until the next morning...
              </p>
              <p>
              Please <Link to="/contactuk">contact</Link> us if you would like to know more. 
              </p>
              <div className="centerImage">
              <GatsbyImage image={Tempel} alt={"Tempel Casa de Nepal"}/>
              </div>
              <div className="centerImage">
              <GatsbyImage image={CasaHuis} alt={"Casa de Nepal house"}/>
              </div>
              <div className="centerImage">
              <GatsbyImage image={OmgevingCasa} alt={"Surroundings Casa de Nepal"}/>
              </div>

              <h3>AMARYLLIS PROJECT</h3>
              <p>
              Guru Premangee is captivated by the beauty of the amaryllis flowers. He wants to 
              start growing and spreading them in Nepal. ‘Because’ he says, ‘Nepali houses and 
              offices need more colorful flowers!’ Right now there is only one variety available 
              in Nepal so there is room for plenty more!
              </p>
              <p>
              To be able to grow them successfully they need a stable and controlled environment, 
              which means a greenhouse with a well-functioning watering system. This is quite a 
              challenge in Nepal. 
              </p>
              <p>
              Are you knowledgeable in managing these types of plants and/or do you have specific 
              knowledge on designing green houses in countries with only very basic facilities? 
              Or do you wish to contribute financially to this project? Then we would love to get 
              in <Link to="/contactuk">contact</Link> with you!
              </p>
              <div className="centerImage">
              <GatsbyImage image={AmarillImage1} alt={"Amaryllis"}/>
              </div>
              <div className="centerImage">
              <GatsbyImage image={AmarillImage2} alt={"Amaryllis 2"}/>
              </div>

              </div>
        </Layout>
    )
}
